.App {
    width: 45%;
    min-height: 100%;
    margin: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

#bg-photo {
    height: 45vh;
    width: 100%;
    z-index: -3;
    position: absolute;
    top: 0;
    left: 0;
    background-position: 0 80%;
    display: block;
    -webkit-filter: blur(4px);
            filter: blur(4px);
}

#content-bg {
    position: fixed;
    display: block;
    top: 0px;
    bottom: 0px;
    width: inherit;
    background-color: var(--bg-color);
    margin-top: 100px;
    z-index: -1;
}

footer {
    color: bisque;
    font-size: small;
    padding-top: 3px;
    height: 20px;
    margin-bottom: 0;
    width: 100%;
    text-align: center;
    margin-top:20px;
}

footer >small{
    
    position: relative;
    height: 100%;
    margin-top:20px;
}

@media screen and (max-width: 1440px) {
    .App {
        width: 70%;
    }
}

@media screen and (max-width: 886px) {
    .App {
        width: 90%;
        padding-right: 0;
    }
}


