#edu-card {
  margin-bottom: 50px;
  padding-top: 25px;
  padding-bottom: 25px;
  width: 100%;
  background-color: var(--content-color);
  border-bottom: 5px solid var(--primary-color);
}

@media screen and (max-width: 886px) {
  #edu-card {
    padding-right: 0;
    margin-top: 30px;
  }
}

.timeline-container {
  margin-left: 40px;
  margin-right: 40px;
  margin-top: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: var(--content-color);
}

.timeline-container * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.timeline-arrow {
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  margin: auto;
  margin-bottom: 0;
  border-bottom: 15px solid var(--primary-color);
}

/* The actual timeline (the vertical ruler) */

.timeline {
  position: relative;
  max-width: 1200px;
}

/* The actual timeline (the vertical ruler) */

.timeline::after {
  content: '';
  position: absolute;
  width: 6px;
  background-color: var(--primary-color);
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -3px;
}

/* Place the container to the left */

.timeline-left {
  left: 0;
}

/* Place the container to the right */

.timeline-right {
  left: 50%;
}

/* Add arrows to the left container (pointing right) */

.timeline-left::before {
  content: "";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  right: 30px;
  border: medium solid var(--primary-color);
  border-width: 10px 0 10px 10px;
  border-color: transparent transparent transparent var(--primary-color);
}

/* Add arrows to the right container (pointing left) */

.timeline-right::before {
  content: "";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  left: 30px;
  border: medium solid var(--primary-color);
  border-width: 10px 10px 10px 0;
  border-color: transparent var(--primary-color) transparent transparent;
}

/* Fix the circle for containers on the right side */

.timeline-right::after {
  left: -16px;
}

/* Container around content */

.el-wrapper {
  position: relative;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
}

.el-container {
  padding: 10px 40px;
  position: relative;
  background-color: inherit;
  width: 50%;
  opacity: 0;
}

.el-anim-in {
  -webkit-transform-origin: top;
  -ms-transform-origin: top;
      transform-origin: top;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
	-webkit-animation: el-anim-in 1s ease 0.3s both;
	        animation: el-anim-in 1s ease 0.3s both;
}

@-webkit-keyframes el-anim-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes el-anim-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/* The actual content */

.el-content {
  padding-right: 20px;
  padding-left: 20px;
  padding-top: 5px;
  position: relative;
  border-radius: 6px;
  background-color: var(--content-color);
  border: 2px solid var(--primary-color);

  -webkit-transform-origin: top;
  -ms-transform-origin: top;
      transform-origin: top;
}

.el-name {
  min-height: 20px;
  font-size: 1.1em;
  font-weight: bold;
  width: 100%;
  border-bottom: 2px solid var(--accent-color);
  margin-top: 10px;
  margin-bottom: 0;
  padding-left: 10px;
  padding-bottom: 5px;
}

.el-name i {
  color: var(--primary-variant2);
}

.el-date {
  margin-top: 10px;
  padding-left: 10px;
  font-weight: bold;
}

.el-about {
  padding-left: 10px;
  padding-bottom: 15px;
  margin-bottom: 0;
}

.el-circle {
  width: 25px;
  height: 25px;
  background-color: var(--primary-color);
  border: 4px solid var(--primary-color);
  top: 15px;
  border-radius: 50%;
  z-index: 1;
  margin-top: 20px;
  margin-left: -12px;
}

@media screen and (max-width: 886px) {
  #edu-card {
    margin-top: 0;
    margin-bottom: 50px;
  }
}

@media screen and (max-width: 633px) {
  .timeline-arrow {
    margin: 0;
    margin-left: 21px;
  }
  /* Place the timelime to the left */
  .timeline::after {
    left: 31px;
  }
  /* Make sure all circles are at the same spot */
  .timeline-left::after, .timeline-right::after {
    left: 15px;
  }
  /* Make all right containers behave like the left ones */
  .timeline-right {
    left: 0%;
  }
  /* Full-width containers */
  .el-container {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }
  /* Make sure that all arrows are pointing leftwards */
  .el-container::before {
    left: 60px;
    border: medium solid var(--content-color);
    border-width: 10px 10px 10px 0;
    border-color: transparent var(--primary-color) transparent transparent;
  }
  .el-circle {
    position: absolute;
    top: 0px;
    left: 30px;
  }
}


