#contact-container {
  height: auto;
}

#contact-card {
  position: relative;
  background-color: var(--content-color);
  padding-top: 20px;
  padding-bottom: 25px;
  border-bottom: 5px solid var(--primary-color);
}

#contact-overlay {
  background-color: var(--content-color);
  color: var(--primary-color);
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

#contact-overlay p {
  text-align: center;
  font-weight: bold;
  font-size: 0.9rem;
}

.fast-spin {
  -webkit-animation: fa-spin 1.2s infinite linear !important;
  animation: fa-spin 1.2s infinite linear !important;
}

@-webkit-keyframes fa-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}

@keyframes fa-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}

#contact-form {
  display: grid;
  width: 90%;
  grid-template-columns: [info] repeat(3, minmax(1fr, auto));
  grid-template: 'f f f' 'a b c' 'd d d' 'e e e';
  grid-column-gap: 10px;
  margin: auto;
  margin-top: 30px;
}

#contact-form input {
  height: 40px;
  border-radius: 6px;
  border: 0;
  border: 1px solid var(--primary-color);
  font-size: 1em;
  text-align: center;
  outline: none;
  grid-row: info;
  font-family: 'Libre Baskerville', serif;
  min-width: 0;
}

#contact-form input:focus {
  border-color: var(--primary-color-hover);
}

#contact-form p {
  color: red;
  text-align: center;
  grid-area: f;
  min-height: 20px;
  margin-top: 0;
}

#contact-form button {
  grid-area: e;
  height: 40px;
  width: 100px;
  margin: auto;
  margin-top: 20px;
}

#contact-form textarea {
  grid-area: d;
  margin-top: 20px;
  resize: none;
  border-radius: 6px;
  border: 1px solid var(--primary-color);
  height: 200px;
  padding: 15px;
  text-align: center;
  font-size: 1em;
  font-family: 'Libre Baskerville', serif;
  outline: none;
  min-width: 0;
}

#contact-form textarea:focus {
  border-color: var(--primary-color-hover);
}

@media screen and (max-width: 886px) {
  #contact-card {
    margin-top: 0;
  }
}

@media screen and (max-width: 633px) {
  #contact-container {
    height: auto;
  }

  #contact-form {
    grid-template-columns: [info] repeat(3, minmax(1fr, auto));
    grid-row-gap: 20px;
    grid-template: 'f' 'a' 'b' 'c' 'd' 'e';
  }
}


