#nav-mob-show {
    width: 100vw;
    height: 70px;
    position: fixed;
    display: -ms-flexbox;
    display: flex;
    left: -0px;
    bottom: -20px;
    -ms-flex-pack: center;
        justify-content: center;
    visibility: hidden;
    z-index: 2;
}

#nav-mob-show .btn {
    width: 40px;
    height: 40px;
    border-radius: 20px;
}

#nav-mob {
    visibility: hidden;
    position: fixed;
    bottom: -120px;
    height: 120px;
    width: 100vw;
    background-color: var(--primary-color);
    display: grid;
    grid-template: "1 2" "3 4";
    grid-template-rows: 60px 60px;
    grid-template-columns: 1fr 1fr;
    left: 0;
    z-index: 2;
}

#nav-mob .btn {
    width: 100%;
    height: 60px;
    border-bottom: 2px solid white;
}

#nav-mob .btn:nth-child(odd) {
    border-right: 2px solid white;
}

#nav-mob .btn:nth-child(3), #nav-mob .btn:nth-child(4) {
    border-bottom: 0;
}

#nav-mob-bg {
    width: 100vw;
    height: 100vh;
    z-index: 1;
    position: fixed;
    top: 0;
    left: 0;
    visibility: hidden;
    opacity: 0;
    background-color: var(--bg-color);
}

#nav {
    display: -ms-flexbox;
    display: flex;
    position: fixed;
    top: 0;
    margin-top: 120px;
    -ms-flex-direction: column;
        flex-direction: column;
    overflow: hidden;
    -webkit-animation: nav-anim-in 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    animation: nav-anim-in 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    width: 150px;
}

@-webkit-keyframes nav-anim-in {
    0% {
        -webkit-transform: translateX(-150px);
        transform: translateX(-150px);
        z-index: -2;
    }
    99% {
        z-index: -2;
    }
    100% {
        -webkit-transform: translateX(0);
        transform: translateX(0);
        z-index: 0;
    }
}

@keyframes nav-anim-in {
    0% {
        -webkit-transform: translateX(-150px);
        transform: translateX(-150px);
        z-index: -2;
    }
    99% {
        z-index: -2;
    }
    100% {
        -webkit-transform: translateX(0px);
        transform: translateX(0px);
        z-index: 0;
    }
}

#nav .btn {
    height: 40px;
    border-bottom: 2px solid var(--content-color);
    background-color: var(--primary-variant2);
    width: 150px;
    text-align: left;
}

#nav .btn:hover {
    background-color: var(--primary-color-hover);
}

.nav-mob-anim-in {
    -webkit-animation: nav-mob-anim-in 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    animation: nav-mob-anim-in 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

@-webkit-keyframes nav-mob-anim-in {
    0% {
        -webkit-transform: translateY(0px);
        transform: translateY(0px);
    }
    100% {
        -webkit-transform: translateY(-120px);
        transform: translateY(-120px);
    }
}

@keyframes nav-mob-anim-in {
    0% {
        -webkit-transform: translateY(0px);
        transform: translateY(0px);
    }
    100% {
        -webkit-transform: translateY(-120px);
        transform: translateY(-120px);
    }
}

.nav-mob-anim-out {
    -webkit-animation: nav-mob-anim-out 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    animation: nav-mob-anim-out 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

@-webkit-keyframes nav-mob-anim-out {
    0% {
        -webkit-transform: translateY(-120px);
        transform: translateY(-120px);
    }
    100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
}

@keyframes nav-mob-anim-out {
    0% {
        -webkit-transform: translateY(-120px);
        transform: translateY(-120px);
    }
    100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
}

.nav-mob-anim-in-btn {
    -webkit-animation: nav-mob-anim-in-btn 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    animation: nav-mob-anim-in-btn 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

@-webkit-keyframes nav-mob-anim-in-btn {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-90deg);
        transform: rotate(-90deg);
    }
}

@keyframes nav-mob-anim-in-btn {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-90deg);
        transform: rotate(-90deg);
    }
}

.nav-mob-anim-out-btn {
    -webkit-animation: nav-mob-anim-out-btn 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    animation: nav-mob-anim-out-btn 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

@-webkit-keyframes nav-mob-anim-out-btn {
    0% {
        -webkit-transform: rotate(-90deg);
        transform: rotate(-90deg);
    }
    100% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
}

@keyframes nav-mob-anim-out-btn {
    0% {
        -webkit-transform: rotate(-90deg);
        transform: rotate(-90deg);
    }
    100% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
}

.nav-mob-anim-in-bg {
    -webkit-animation: nav-mob-anim-in-bg 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    animation: nav-mob-anim-in-bg 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

@-webkit-keyframes nav-mob-anim-in-bg {
    0% {
        opacity: 0;
        visibility: hidden;
    }
    100% {
        opacity: 0.5;
        visibility: visible;
    }
}

@keyframes nav-mob-anim-in-bg {
    0% {
        opacity: 0;
        visibility: hidden;
    }
    100% {
        opacity: 0.5;
        visibility: visible;
    }
}

.nav-mob-anim-out-bg {
    -webkit-animation: nav-mob-anim-out-bg 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    animation: nav-mob-anim-out-bg 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

@-webkit-keyframes nav-mob-anim-out-bg {
    0% {
        opacity: 0.5;
        visibility: visible;
    }
    100% {
        opacity: 0;
        visibility: hidden;
    }
}

@keyframes nav-mob-anim-out-bg {
    0% {
        opacity: 0.5;
        visibility: visible;
    }
    100% {
        opacity: 0;
        visibility: hidden;
    }
}

@media screen and (max-width: 886px)
    {
    #nav-mob-show {
        visibility: visible;
    }
    #nav-mob {
        visibility: visible;
    }
    #nav-mob-bg {
        visibility: hidden;
    }
    #nav {
        visibility: hidden;
    }
}




