#skills-card {
  padding-top: 25px;
  padding-bottom: 25px;
  width: 100%;
  background-color: var(--content-color);

  border-bottom: 5px solid var(--primary-color);
  margin-bottom: 50px;
}

#skills-container {
  margin: auto;
  margin-top: 30px;
  width:90%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-column-gap: 15px;
}

.skill {
  margin-bottom: 10px;
}

.skill-name {
  padding-left: 20px;
  margin-bottom: 5px;
  padding-bottom: 5px;
}

.skill-name:not(i)
{
  font-weight: bold;
}

.skill-name i {
  color: var(--primary-variant2);
}

.skill-grade-container {
  width: 90%;
  background-color: var(--content-color);
  height: 5px;
  margin-top: 0;
  margin-left: 10px;
  margin-bottom: 10px;
}

.skill-grade {
  background-color: var(--primary-color);
  height: 100%;
  visibility: hidden;
}

.skill-anim-in {
  -webkit-transform-origin: 0% 50%;
      -ms-transform-origin: 0% 50%;
          transform-origin: 0% 50%;
  
  -webkit-animation: skill-anim-in 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 0.2s both;
    animation: skill-anim-in 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 0.2s both;
}

@-webkit-keyframes skill-anim-in {
  0% {
    -webkit-transform: scale(0,1);
    transform: scale(0,1);
    visibility: visible;
}
100% {
    -webkit-transform: scale(1,1);
    transform: scale(1,1);
    visibility: visible;
}
}

@keyframes skill-anim-in {
  0% {
      -webkit-transform: scale(0,1);
      transform: scale(0,1);
      visibility: visible;
  }
  100% {
      -webkit-transform: scale(1,1);
      transform: scale(1,1);
      visibility: visible;
  }
}

.skill-about {
  padding-right: 10px;
}

@media screen and (max-width: 886px) {
  #skills-card {
    margin-top: 0;
    margin-bottom: 50px;
  }
}


