#me-card {
    padding-top: 25px;
    padding-bottom: 25px;
    width: 100%;
    background-color: var(--content-color);
    display: grid;
    grid-template-areas: "p n n" "p il ir" "p a a" "b a a";
    grid-template-columns: minmax(260px, auto) 1fr 1fr;
    grid-template-rows: 70px 150px minmax(120px, 120px) auto;
    margin-top: 100px;
    border-bottom: 5px solid var(--primary-color);
    margin-bottom: 50px;
}

#me-photo {
    grid-area: p;
    display: -ms-flexbox;
    display: flex;
    height: 300px;
    width: 200px;
    margin: auto;
    margin-bottom: 0;
}

#me-photo>img {
    -o-object-fit: cover;
       object-fit: cover;
    overflow: hidden;
    margin-bottom: 25px;
    width: 220px;
    border: 3px solid var(--primary-color);
}

#me-contact-btn {
    grid-area: b;
}

#me-contact-btn>button {
    width: 200px;
    height: 50px;
    margin: auto;
    display: block;
}

#me-name {
    grid-area: n;
    overflow: hidden;
}

#me-name p {
    padding-left: 20px;
    padding-right: 20px;
}

#me-name p:first-child {
    color: var(--primary-color);
    font-size: 2em;
    margin-top: 0;
    padding-bottom: 0;
    margin-bottom: 0;
    font-weight: bold;
}

#me-name p:not(:first-child) {
    margin-top: 0px;
    border-top: 2px solid var(--accent-color);
    padding-top: 5px;
    margin-right: 25px;
}

.me-section {
    font-weight: bold;
}

.me-section i {
    color: var(--primary-color);
}

#me-info-l {
    grid-area: il;
}

#me-info-l p {
    margin-left: 30px;
    margin-bottom: 0;
}

#me-info-r {
    grid-area: ir;
}

#me-info-r p {
    margin-bottom: 0;
}

#me-info-l p:not(.me-section), #me-info-r p:not(.me-section) {
    padding-bottom: 20px;
    margin-bottom: 5px;
    margin-right: 20px;
    padding-top: 8px;
    margin-top: 0;
    border-top: 1px solid var(--primary-color);
    max-width: 180px;
}

#me-about {
    grid-area: a;
    margin-right: 25px;
}

#me-about p {
    padding-left: 20px;
    padding-right: 20px;
}

#me-about p:first-child {
    border-bottom: 1px solid var(--primary-color);
}

@media screen and (max-width: 886px) {
    #me-card {
        grid-template: "n n" "p il" "p ir" "b ir" "a a";
        margin-bottom: 50px;
        margin-top: 10px;
        padding: 0;
    }
    #me-photo {
        -ms-flex-direction: row-reverse;
            flex-direction: row-reverse;
        margin-right: 40px;
    }
    #me-contact-btn {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: row-reverse;
            flex-direction: row-reverse;
        margin-right: 40px;
    }
    #me-contact-btn .btn {
        margin-right: 0;
    }
    #me-name {
        margin: 30px;
        margin-bottom: 10px;
    }
    #me-name p:not(:first-child) {
        padding-right: 20px;
        margin-right: 0;
    }
    #me-info-r p, #me-info-l p {
        margin-left: 0;
    }
    #me-about {
        margin-left: 30px;
        margin-right: 30px;
        margin-bottom: 30px;
    }
}

@media screen and (max-width: 633px) {
    #me-card {
        grid-template: "n" "p" "b" "il" "ir" "a";
    }
    #me-photo {
        margin: auto;
    }
    #me-contact-btn {
        margin: auto;
    }
    #me-info-r, #me-info-l {
        margin-left: 30px;
        margin-right: 30px;
    }
    #me-info-l p {
        margin-left: 0;
    }
    #me-info-r p, #me-info-l p {
        padding-left: 20px;
    }
    #me-info-r p:not(.me-section), #me-info-l p:not(.me-section) {
        max-width: 10000px;
        margin-right: 0;
        padding-bottom: 2px;
    }
}


