:root {
  --bg-color: #212121;
  --content-color:#FAFAFA;
  --content-variant1: #BDBDBD;
  --font-color:#212121;
  --accent-color:#FF9800;
  --primary-color:#00695C;
  --primary-color-hover: #004D40;
  --primary-variant1:#00695C;
  --primary-variant2:#00695C;
  
}

html {
  position: relative;
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Libre Baskerville', serif;
  font-size: 0.85rem;
  color: #000000;
  background-color: var(--bg-color);
}

#root {
  min-height: 100%;
}

.card-title {
  color: var(--primary-variant2);
  font-size: 2em;
  border-bottom: 2px solid var(--accent-color);
  margin: auto;
  width: 90%;
  font-weight: bold;
  padding-left: 20px;
}

.btn {
  font-family: 'Libre Baskerville', serif;
  font-size: 14px;
  background-color: var(--primary-color);
  color: white;
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  border:0;
  border-radius: 2px;
  outline:0;
}

.btn:hover {
  background-color: var(--primary-color-hover);
}

@media screen and (max-width: 350px) {
  body {
    font-size: 0.75rem;
  }
}


